.app-header {
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: baseline;
          align-items: baseline;
}
.todo-list-item {
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

.todo-list-text {
  word-break: break-all;
}

.todo-list-text--important {
  color: darkblue;
  font-weight: bold;
}

.todo-list-text--finished {
  text-decoration: line-through;
}

.todo-list-item__btns {
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width: 67px;
  margin-left: 10px;
}

.todo-list-item__btns button {
  width: 31px;
  height: 31px;
}

.todo-list-item__btn {
  background-color: transparent;
  border: none;
  padding: 0;
  margin: 0;
  cursor: pointer;
  text-align: left;
  transition: box-shadow .15s;
}

.todo-list-item__btn:focus {
  outline: none;
  box-shadow: 0px 3px 0 0rem rgba(0,123,255,.25);
}
.search-panel {
  width: 100%;
}
.item-status-filter__btn--active {
  background-color: blue;
}

.todo-app {
  max-width: 520px;
  width: 90%;
  min-width: 320px;
  margin: 50px auto;
}
